.cart-item{
    text-align: center;

    width:30%;

    justify-content: space-evenly;
    align-items: center;
    margin: 0 0 1rem 0;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.cart{
    padding:100px;
}
input{
    width:10%;
}
button{
    margin:10px;
}
.cart-count {
    background-color: red;
    color: white;
    font-size: 12px;
    font-weight: bold;
    border-radius: 50%;
    padding: 2px 6px;
    margin-left: 5px;
}
